//List

.board_list {
  position: relative;
  float: left;
  width: 100%;
  height: 100%;

  .list {
    border-top: 1px solid #ababab;
    padding-top: 10px;
    font-family: "Noto Sans KR", sans-serif;
    a {
      color: #000;
      text-decoration: none;

      .catname {
        margin: 0 10px 0 10px;
        color: rgb(243, 147, 22);
      }
    }

    h4 {
      span {
        font-size: 0.9em;
        margin: 3px;
        color: black;
      }
    }
  }

  .btn {
    display: inline-block;
    padding: 0 30px;
    font-size: 15px;
    font-weight: 400;
    background: transparent;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    text-transform: uppercase;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }

  .btn_dark {
    border-radius: 10px;
    background: #555;
    color: #fff;
  }

  .btn_dark:hover,
  .btn_dark:focus {
    background: #373737;
    border-color: #373737;
    color: #fff;
  }

  .btn_dark {
    background: #555;
    color: #fff;
  }

  .btn_dark:hover,
  .btn_dark:focus {
    background: #373737;
    border-color: #373737;
    color: #fff;
  }

  .board_search {
    padding: 15px 0 0 150px;
    // serach
    .search_input {
      padding: 2px;
      margin-right: 5px;
      margin-top: 15px;
      margin-left: 15%;
      width: 25%;
    }
  }
}
