.comments {
  margin: 15px 0;
  .write {
    input {
      overflow: hidden;
      width: 70%;
      padding: 0 0 20px 20px;
      min-height: 50px;
      border: 1px solid #999;
      border-radius: 0;
      margin-left: 10%;
      background-color: rgba(239, 239, 239);
      color: #000;
    }
    button {
      position: absolute;
      width: 10%;
      height: 72px;
      margin-left: 0.2px;
      background-color: #fff;
      border: 1px solid #333;
      border-radius: 0;
    }
  }
  .comment_count {
    margin-left: 10%;
    margin-bottom: 1%;
    span {
      font-weight: 1000;
      color: rgb(176, 127, 96);
    }
  }
  .comment {
    // margin: 0 0 0 20px;
    position: relative;
    padding: 20px 0;
    button {
      background-color: transparent;
      border: none;
      margin: 0 0 0 10px;

      :hover {
        color: orangered;
      }
    }
    &_list {
      width: 99%;
      border-top: 1px solid rgb(155, 146, 146);
    }
    &_desc {
      font-size: 0.9rem;
      margin: 5px 0 10px 15px;
      width: 1000px;
      word-break: keep-all;
      word-wrap: break-word;
      //   background-color: red;
    }
    &_name {
      margin-left: 15px;
      color: rgb(73, 68, 68);
      font-weight: 700;
      //   background-color: red;
    }
    &_date {
      color: #999;
      font-size: 0.6em;
      font-weight: 700;
      margin-left: 20px;
      //   background-color: red;
    }
  }
}

